body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  text-align: center;
}

.app-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 24px;
  color: white;
}

.app-link {
  color: #61dafb;
}

.dropdown {
  width: 250px;
}

.select-footer {
  padding: 15px 5px;
  border-top: 1px solid #555;
}

.selector {
  padding: 10px;
  font-weight: bold;
  color: #28292b;
  background: #eee;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.selector-items {
  position: relative;
  top: -50%;
}

.selector-item {
  color: #28292b;
  background: #eee;
  padding: 5px;
}

.selector-item:hover {
  color: #513dff;
  background: #ccc;
}

.selector-item, .selector {
  text-align: left;
  cursor: pointer;
}

.items-closed {
  display: none;
}

.items-opened {
  display: block;
}

.selector span {
  float: right;
  font-size: 16px;
  margin: 5px;
}

.open::after {
  content: "\25BC";
}

.close::after {
  content: "\25B2";
}

.opened {
  box-shadow: 0px 5px 5px 0px rgba(70,100,100,0.8);
  border-bottom: 1px solid #999;
  position:relative;
  z-index: 2;
}
